.color-grey {
    color: #CFCFCF !important;
    font-size: 16px !important;
    white-space: nowrap;
}

.color-grey strong{
    color: black;
    font-size: 20px
}


.active, .dropdown-item.active{
    color: #00A8AB !important  ;
    -webkit-transition: color 0.4s ease 0s;
    transition: color 0.4s ease 0s;
    background-color: #ffffff
}
a:hover,.dropdown-item.hover  {
    color: #8ed4d5!important;
    -webkit-transition: color 0.2s ease 0s;
    transition: color 0.2s ease 0s;
}
a{
    color: #000000 !important;
}
.borderr {
    border: 1px solid #000000;
    background-color: #ffffff;
    border-radius: 0;
}
.Black-block {
        background-color: black;
        background-clip: padding-box;
        background-size: 100%;
        color: #ffffff;
}

.color2 {
    display: block;
    color: #00A8AB !important
}
.About {
    border: 1px solid black;
}
.who{
    position: relative;
    display: block;

}
.rewiew{
    flex-direction: column
}
.rotatedBlock {
	-webkit-transform: rotate(-4deg); /* для IE9 */
	transform: rotate(-4deg); /* на будущее */
}
.kosoy{
    font-style: oblique;
}
.line {
    box-sizing: border-box;
    border-top: 1px solid #000000;
}
#link div :hover {
    color: #8ed4d5!important;
    -webkit-transition: color 0.2s ease 0s;
    transition: color 0.2s ease 0s;
}

.image {
    width: 864px;
    height: 442px;
    background-size: 100%;
}




.Wrapped {
    left: 0;
    bottom: 0;
    width: 100%;
}


.border-3px-white {
    border: solid #ffffff 3px;
}

@media (min-width: 0px) {
    .Img-wrap {
        position: relative;
        left: 15px;
        bottom: 0;
        width: 100%;
    }
}

@media (min-width: 992px) {
    .Img-wrap {
        position: absolute;
        left: 15px;
        bottom: 0;
        width: 100%;
    }
}
.navigation-link{
    text-align: center;
    color: #fff;
    background-color: #0000004d;
    
}

.fs16 {
    font-size: 16px;

}

.Info {
    border: 1px solid black;

}
.slick-slide {
    height: 100%;
    width: 100%;
}

.slick-slide img {
    height: 100%;
    width: 100%;
}
.slick-next:before,
.slick-prev:before {
    font-size: 20px;
    line-height: 1;
    opacity: 0.75;
    color: #000000;
}

